import install from './vue-plugin'
import asyncPlaceModules from './placeholder_module_config/async.js'
import basicPlaceModules from './placeholder_module_config/basic.js'

function Modularize (options) {
  const { basicModules, asyncModules } = options || {}
  this.basicModules = basicModules || basicPlaceModules
  this.asyncModules = asyncModules || asyncPlaceModules
}

Modularize.install = install

export default Modularize
