import setRouterBeforeEachGuard from './router-guard'

function install (Vue, { router }) {
  if (!router) {
    throw new Error('Modularize Plugin: Vue.use(Modularize, options), options.router is required')
  }
  Vue.prototype._appRegisteredAsyncModules = []
  Vue.prototype.registerModule = function registerModule (moduleName, module) {
    this._appRegisteredAsyncModules.push(moduleName)
    const { ajax: ajaxConfig, router: routerConfig, store: storeConfig } = module
    const { ajax, router, store } = this.$options
    if (ajaxConfig) {
      const isFunConfig = typeof ajaxConfig === 'function'
      const config = isFunConfig ? ajaxConfig(ajax) : ajaxConfig
      ajax.extendRest(moduleName, config)
    }
    if (routerConfig) {
      router.addRoutes(routerConfig)
    }
    if (storeConfig) {
      store.registerModule(moduleName, storeConfig)
    }
  }

  setRouterBeforeEachGuard(router)

  Vue.mixin({
    created: function () {
      if (!this.$options.modules) return
      const {
        modules: {
          basicModules = {}
        } = {}
      } = this.$options
      // 
      Object.keys(basicModules).forEach(moduleName => {
        this.registerModule(moduleName, basicModules[moduleName])
        this._appRegisteredAsyncModules.push(moduleName)
      })
    }
  })
}

export default install
