


















import { Component, Vue, Watch, Prop } from 'vue-property-decorator'

interface route {
  path?: string
  title: string
  query?: { [k: string]: string }
}

@Component
export default class BreadCrumb extends Vue {
  @Prop({default: () => []}) pathList: route[]
  @Prop(String) shortName: string

  handleNavigate(item) {
    this.$emit('nav', item)
  }
}
