function normalizeName (name) {
  const normalizedName = name
    .replace(/[_-]/g, '')
    .toLowerCase()
  return [name, normalizedName]
}

// carVisits/car-visits/.../carvisits/.../carVisits/...
// carVisits
function findRealModuleName (moduleMap, tempModuleName) {
  const normalizedModuleNameList = Object.keys(moduleMap).map(name => normalizeName(name))
  const normalizedModName = normalizeName(tempModuleName)[1]
  const moduleName = normalizedModuleNameList
    .find(item => item[1] === normalizedModName)
  return moduleName && moduleName[0]
}

export default function setRouterBeforeEachGuard (router) {
  router.beforeEach((to, from, next) => {
    // 
    const { path, matched } = to
    const {
      modules: {
        asyncModules: moduleMap = {},
        basicModules: basicMap = {}
      } = {}
    } = router.app.$options

    if (matched.length === 0) { // 
      const tempModuleName = path.split('/')[1]
      const moduleName = findRealModuleName(moduleMap, tempModuleName)
      const isRegistered = router.app._appRegisteredAsyncModules.includes(moduleName)
      if (isRegistered) return next() // 
      if (moduleName) {
        moduleMap[moduleName]()
          .then(modObj => {
            router.app.registerModule(moduleName, modObj.default)
            next(to)
          })
          .catch(err => {
            next(err)
          })
      } else {
        // router-guardmixin
        // 
        const tempModuleName = path.split('/')[1]
        const moduleName = findRealModuleName(basicMap, tempModuleName)
        const isRegistered = router.app._appRegisteredAsyncModules.includes(moduleName)
        if (isRegistered) return next() // 
        if (moduleName) { // 
          router.app.registerModule(moduleName, basicMap[moduleName])
          next(to)
        } else { // 
          console.warn(`module '${tempModuleName}' not found!`)
        }
      }
    } else {
      next()
    }
  })
  // 
  const func = router.beforeHooks.pop()
  router.beforeHooks.unshift(func)
}
