import cloneDeep from 'lodash/cloneDeep';
import forIn from 'lodash/forIn';

/*
 * util helper methods
 */
export default {
  /**
   * 
   */
  sendCaptcha(timer) {
    if (timer.seconds > 0) {
      timer.seconds -= 1;
      timer.text = `: ${timer.seconds} `;
      clearTimeout(timer.secondsTimer);
      timer.secondsTimer = setTimeout(() => {
        this.sendCaptcha(timer);
      }, 1000);
    } else {
      clearTimeout(timer.secondsTimer);
      timer.secondsTimer = null;
      timer.text = '';
    }
    return timer;
  },
  /**
   * (M)(d)(h)(m)(s)(q)  1-2 
   * (y) 1-4 (S) 1 ( 1-3 )
   * 
   * formatTime(new Date(), 'yyyy-MM-dd hh:mm:ss.S') ==> 2006-07-02 08:09:04.423
   * formatTime(new Date(), 'yyyy-M-d h:m:s.S')      ==> 2006-7-2 8:9:4.18
   * http://www.cnblogs.com/zhangpengshou/archive/2012/07/19/2599053.html
   */
  formatTime(time, fmt) {
    if (!time || !fmt) {
      return '';
    }
    if (typeof time === 'string') {
      time = time.replace(/-/g, '/');
      time = new Date(time);
    }
    const o = {
      'M+': time.getMonth() + 1, // 
      'd+': time.getDate(), // 
      'h+': time.getHours(), // 
      'm+': time.getMinutes(), // 
      's+': time.getSeconds(), // 
      'q+': Math.floor((time.getMonth() + 3) / 3), // 
      S: time.getMilliseconds(), // 
    };
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (`${time.getFullYear()}`).substr(4 - RegExp.$1.length));
    }
    Object.keys(o).forEach(k => {
      if (new RegExp(`(${k})`).test(fmt)) {
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : ((`00${o[k]}`).substr((`${o[k]}`).length)));
      }
    });
    return fmt;
  },

  /**
   *  . 2017.5.9
   * @param time 
   * @param join  .
   */
  formatDate(time = null, join) {
    join = join || '.';
    const date = time === null ? new Date() : new Date(time);
    const timeArr = [date.getFullYear(), date.getMonth() + 1, date.getDate()];
    return timeArr.join(join);
  },
  go (url, $router) {
    if (/^javas/.test(url) || !url) return;
    const useRouter = typeof url === 'object' || ($router && typeof url === 'string' && !/http/.test(url));
    if (useRouter) {
      $router.push(url);
    } else {
      window.location.href = url;
    }
  },
  getScrollTop() {
    let scrollTop = 0;
    let bodyScrollTop = 0;
    let documentScrollTop = 0;
    if (document.body) {
      bodyScrollTop = document.body.scrollTop;
    }
    if (document.documentElement) {
      documentScrollTop = document.documentElement.scrollTop;
    }
    scrollTop = bodyScrollTop - documentScrollTop > 0 ? bodyScrollTop : documentScrollTop;
    return scrollTop;
  },
  /**
   *  2018-12-02 09:00  2018-12-02 09:00 -- 0930
   * @param timeStr 2018-12-02 09:00 || 09:00
   * @returns {string} 2018-12-02 09:00 -- 0930 || 09:00 -- 0930
   */
  addHalfHour(timeStr) {
    if (!timeStr || !timeStr.indexOf(':')) {
      return timeStr;
    }
    const arr = timeStr.split(':');
    const toTen = (num) => (num < 10 ? `0${num}` : `${num}`);
    const hour = arr[0].length === 2 ? arr[0] : arr[0].substr(11);
    const halfHourLater = +arr[1] >= 30 ? `${toTen(+hour + 1)}:00` : `${hour}:30`;
    return `${timeStr} - ${halfHourLater.length <= 5 ? halfHourLater : halfHourLater.substr(11)}`;
  },
  /**
   * 
   * @param {string} protocol http:https:ftp:
   * @param {string} host  www.baidu.com
   * @param {string} url
   *  'https://www.baidu.com/more'
   * openNewTab('https', 'www.baidu.com', 'more')
   */
  openNewTab(protocol, host, url) {
    const openHref = `${protocol}//${host}/${url}`;
    window.open(openHref);
    return openHref;
  },
  /**
   * 
   * @param {object} obj obj
   * ""() 0 
   * nullNaNundefined
   */
  clearNullProp(obj) {
    const _this = this;
    // 
    const _obj = cloneDeep(obj);
    forIn(_obj, (val, key) => {
      if (!val && val !== 0 && val !== false) {
        delete _obj[key];
      }

      if (_obj[key] instanceof Object) {
        _obj[key] = _this.clearNullProp(_obj[key]);
        //  follow: {}  follow 
        if (!Object.keys(_obj[key]).length) {
          delete _obj[key];
        }
      }
    });
    return _obj;
  },
  /* 
  ** @param sub-len 
  */
  arrayList(sub, len) {
    const arraylist = [];
    for (sub; sub <= len; sub++) {
      arraylist.push(sub);
    }
    return arraylist;
  },
  /**
   * 
   * @param {String} value 'yyyy-MM-dd'
   */
  addOneYear(value) {
    const oneYear = new Date(value);
    oneYear.setFullYear(oneYear.getFullYear() + 1);
    oneYear.setDate(oneYear.getDate() - 1);
    return oneYear;
  },
  /**
   * 
   */
  disableScroll() {
    document.documentElement.style.overflowY = 'hidden';
    // 
    if (document.body.scrollHeight > (window.innerHeight || document.documentElement.clientHeight)) {
      document.documentElement.style.paddingRight = '16px';
    }
  },
  enableScroll() {
    document.documentElement.style.overflowY = 'auto';
    if (document.body.scrollHeight > (window.innerHeight || document.documentElement.clientHeight)) {
      document.documentElement.style.paddingRight = '0';
    }
  },
  downloadImage(src, name) {
    const image = new Image();
    //  canvas 
    image.setAttribute('crossOrigin', 'anonymous');
    image.onload = function () {
      const canvas = document.createElement('canvas');
      canvas.width = image.width;
      canvas.height = image.height;
      const context = canvas.getContext('2d');
      context.drawImage(image, 0, 0, image.width, image.height);
      const url = canvas.toDataURL('image/png');
      const a = document.createElement('a');
      const event = new MouseEvent('click');
      a.download = name || '';
      a.href = url;
      a.dispatchEvent(event);
    };
    image.src = src;
  },
  getQueryVariable(name) {
    const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    const r = window.location.search.substr(1).match(reg);
    if (r !== null) return unescape(r[2]); return null;
  },
  /**
   * 
   * @param val
  */
  fenToWan (val) {
    return val ? `${(val / 1000000).toFixed(2)}` : '--';
  },
};



