const responseErrorInterceptor = (ctx: typeof ajax) => (err) => {
  const { response } = err;
  if (response) {
    const { status } = response;
    switch (status) {
      case 404:
        ctx.showTips('404');
        break;
      case 500:
        ctx.showTips('500');
        break;
      case 504:
        ctx.showTips('');
        break;
      case 400: // csrf-token
      case 401: // 
        ctx.showTips('');
        operNoneLogin(ctx);
        break;
      default:
        ctx.showTips(`error:${err.response.status}`);
        break;
    }
    const res = normalizeRes(err.response);
    // return Promise.reject(res.data); // 
    return res;
  }
  return null;
};

